input[type='radio'],
input[type='checkbox'] {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: var(--space-6);
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: var(--space-6);
}

/* CHECKBOX */
.custom-checkbox {
  border: 2px solid var(--colors-pukepoto);
  border-radius: 5px;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-block;
  height: var(--space-6);
  margin-top: 2px;
  margin-right: var(--space-6);
  min-width: var(--space-6);
  position: relative;
  width: var(--space-6);
}

/* CHECKBOX UNCHECHECKED */
.custom-checkbox.error.hover,
.custom-checkbox.hover {
  background: var(--colors-tertiary-25);
}
.custom-checkbox.focus {
  outline: 4px solid var(--colors-outline);
  background: var(--colors-tertiary-25);
}
.custom-checkbox.error {
  border-color: var(--colors-whero);
}
.custom-checkbox.disabled {
  border-color: var(--colors-primary-50);
}

/* CHECKBOX CHECKED */
.custom-checkbox.checked {
  background: var(--colors-tertiary-100);
  border-color: var(--colors-tertiary-100);
}
.custom-checkbox.checked.focus {
  background: var(--colors-tertiary-100);
}
.custom-checkbox.checked.hover {
  background: var(--colors-tertiary-110);
  border-color: var(--colors-tertiary-110);
}
.custom-checkbox.checked.disabled {
  border: 2px solid var(--colors-tertiary-25);
  background: var(--colors-tertiary-25);
}
.custom-checkbox.checked.error {
  border: 2px solid var(--colors-whero);
}
.custom-checkbox.checked:before {
  background: url('../images/icons/tick.svg') no-repeat center center/cover;
  content: '';
  height: 10px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 14px;
}

/* CHECKBOX CONTAINER */
.checkbox-input {
  border: 2px solid var(--colors-ma);
  background-color: var(--colors-ma);
  cursor: pointer;
  display: flex;
  font-weight: var(--fontWeights-regular);
  border-radius: var(--radii-default);
}
.checkbox-input.disabled {
  color: var(--colors-tertiary-25);
}
.checkbox-input.error {
  color: var(--colors-whero);
}
.checkbox-input.focus {
  border-color: var(--colors-kahurangi);
}

/* RADIO */
.custom-radio {
  border: 2px solid var(--colors-pukepoto);
  border-radius: 100%;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-block;
  height: var(--space-6);
  margin-right: var(--space-6);
  min-width: var(--space-6);
  position: relative;
  width: var(--space-6);
}

/* RADIO UNCHECKED */
.custom-radio.focus {
  background-color: var(--colors-tertiary-25);
}

/* RADIO CHECKED */
.custom-radio.checked {
  background-color: var(--colors-pukepoto);
  border: 8px solid var(--colors-kowhai);
}
.custom-radio.focus.checked {
  outline: auto;
}

.selector-input-group {
  cursor: pointer;
  display: flex;
  font-weight: var(--fontWeights-regular);
  padding: var(--space-4);
  white-space: pre-wrap;
}

.form-section > * .selector-input-group {
  padding: 0;
}
