.step-list {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: var(--space-6);
  margin-bottom: var(--space-2);
}

.step-list:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  background-color: var(--colors-pukepoto);
  height: 1px;
}

.step {
  position: relative;
  width: 30px;
  height: 30px;
  background-color: var(--colors-orangi-medium);
  color: var(--colors-pukepoto);
  border: 1px solid var(--colors-pukepoto);
  border-radius: var(--radii-round);
  margin-right: 0;
}

.step-complete {
  background: var(--colors-pukepoto);
  color: var(--colors-ma);
}

.step .step-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: var(--fontSizes-h3);
  font-weight: var(--fontWeights-bold);
}
