.yes-no-button {
  flex: 50%;
  max-width: 21.6rem;
  background-color: var(--colors-ma);
  border-radius: var(--radii-default) 0 0 var(--radii-default);
  border: 1px solid var(--colors-pukepoto);
  color: var(--colors-pukepoto);
  padding: var(--space-3) var(--space-14);
  cursor: pointer;
  height: var(--space-14);
  font-size: var(--fontSizes-p);
}

.yes-no-button.error {
  border-color: var(--colors-whero);
  color: var(--colors-whero);
}

.yes-no-button + .yes-no-button {
  border-radius: 0 var(--radii-default) var(--radii-default) 0;
  border-left-width: 0;
}

.yes-no-button:hover {
  background-color: var(--colors-orangi);
}

.yes-no-button.selected:hover {
  border-color: var(--colors-korora);
  background-color: var(--colors-korora);
}

.yes-no-button:focus {
  box-shadow: var(--shadows-outline);
  outline: none;
  z-index: 2; /* Necessary for the outline border not to be hidden by the other button */
}

.yes-no-button.selected {
  background-color: var(--colors-pukepoto);
  color: var(--colors-ma);
}

@media screen and (min-width: 480px) {
  .yes-no-button {
    flex: none;
    min-width: var(--space-38);
  }
}
