.time-picker-input-ie {
  align-items: center;
  display: flex;
  flex-direction: row;
  padding: 0 var(--space-4);
}

.time-picker-input-ie > input[type='number'] {
  background-color: transparent;
  border: none;
  border-radius: 0;
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  padding: var(--space-3) 0;
  text-align: center;
  width: var(--space-8);
}

.time-picker-input-ie > .separator {
  margin: 0 var(--space-3);
}

.time-picker-input-ie > .select-ampm {
  margin-left: var(--space-3);
  width: var(--space-20);
}

.time-picker-input-ie > .select-ampm > div {
  border: none !important;
}

.time-picker-input-ie > .select-ampm > div > div {
  display: flex !important;
}

.time-picker-input-ie > .clock-icon {
  margin-left: var(--space-3);
}
