.select-container.invalid .select__control {
  border-color: var(--input-border-color-invalid);
}

.select-container .select__control {
  margin: var(--space-2) 0;
  border: var(--input-border);
  border-radius: var(--radii-sm);
  cursor: pointer;
}

.select-container .select__control:hover {
  border: var(--input-border);
}

.select-container .select__control.select__control--is-focused {
  box-shadow: var(--shadows-outline);
}

.select-container .select__control.select__control--is-focused input {
  box-shadow: none;
}

.select-container .select__control .select__value-container {
  padding: var(--space-3) var(--space-4);
}

.select-container
  .select__control
  .select__value-container
  .select__single-value {
  color: var(--font-color);
}

.select-container
  .select__control
  .select__value-container
  .select__input-container {
  margin: 0;
  padding: 0;
}

.select-container .select__control .select__indicator-separator {
  display: none;
}

.select-container
  .select__control
  .select__indicator.select__dropdown-indicator {
  color: var(--colors-pukepoto);
}

.select-container
  .select__control.select__control--menu-is-open
  .select__indicator.select__dropdown-indicator {
  transform: rotate(180deg);
}

.select-container.is-invalid .select__control {
  border-color: var(--input-border-color-invalid);
}

.select-container.is-invalid .select__control svg {
  fill: var(--input-border-color-invalid);
}

.select-container .select__menu {
  position: relative;
  margin-top: 0;
  border: var(--input-border);
  box-shadow: none;
}

.select-container .select__menu .select__menu-list {
  padding: 0;
  border-radius: var(--radii-sm);
}

.select-container .select__menu .select__option {
  cursor: pointer;
}

.select-container .select__menu .select__option:nth-child(odd) {
  background-color: #f0f8f9;
}

.select-container .select__menu .select__option:nth-child(even) {
  background-color: var(--colors-ma);
}

.select-container .select__menu .select__option.select__option--is-selected {
  color: var(--font-color);
  font-weight: var(--fontWeights-semibold);
}

.select-container .select__menu .select__option.select__option--is-focused {
  color: var(--colors-ma);
  background-color: var(--colors-korora);
}
