.select-country {
  width: 140px;
  margin-right: var(--space-4);
}

.select-country .select__control {
  width: 140px;
}

.select-container.select-country .select__control .select__value-container {
  padding-right: 0;
}

.select-container.select-country .select__menu {
  position: absolute;
  width: 225%;
  max-width: calc(100vw - var(--sizes-container-padding-sm));
}

.flag {
  width: var(--sizes-8);
}
