.footer {
  background-color: var(--colors-ma);
  padding: var(--space-8) var(--space-4);
  z-index: 1;
}

.footer-container {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  max-width: 1680px;
  width: calc(100% - var(--sizes-container-padding-sm));
  min-height: 100%;
}

.footer-content {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.footer-section:not(:last-child) {
  margin-bottom: var(--space-6);
}

.footer-heading {
  font-size: var(--fontSizes-p);
  font-family: var(--fonts-body);
  font-weight: var(--fontWeights-bold);
  line-height: var(--lineHeights-base);
  color: var(--colors-pukepoto);
  margin-bottom: var(--space-2);
}
.footer-primary-links,
.footer-secondary-links {
  margin: 0;
  padding: 0;
  list-style: outside none none;
}

.footer-primary-links {
  display: grid;
  gap: var(--space-1);
  font-family: var(--fonts-body);
  font-size: var(--fontSizes-p);
  line-height: var(--lineHeights-base);
}

.footer-secondary-links {
  position: relative;
  display: grid;
  row-gap: var(--space-2);
  padding-bottom: var(--space-6);
  font-family: var(--fonts-body);
  line-height: var(--lineHeights-base);
  font-size: 1.6rem;
}

.footer-link {
  color: var(--colors-pukepoto);
  font-family: var(--fonts-body);
  font-weight: var(--fontWeights-normal);
  outline: currentcolor none medium;
  text-decoration: none;
  transition: var(--transitions-button);
}

.footer-link:hover {
  text-decoration: underline;
}

.footer-divider {
  margin-top: var(--space-8);
  margin-bottom: var(--space-6);
  border-color: var(--colors-orangi-dark);
}

.footer-logo-link {
  width: 23.2rem;
  display: block;
  outline: currentcolor none medium;
  transition: var(--transitions-button);
}

.footer-logo {
  width: 100%;
  transition: inherit;
}

.footer-logo:hover {
  opacity: 0.6;
}

@media screen and (min-width: 768px) {
  .footer {
    padding: var(--space-12) var(--space-0);
  }

  .footer-container {
    width: calc(100% - var(--sizes-container-padding-md));
  }

  .footer-content {
    flex-direction: row;
  }

  .footer-heading {
    margin-bottom: var(--space-4);
  }

  .footer-divider {
    margin-top: var(--space-10);
    margin-bottom: var(--space-3);
  }

  .footer-secondary-links {
    padding-bottom: 0;
    /* NOTE: if number of links in `FOOTER_SECONDARY_LINKS` changes, update this to match */
    grid-template-columns: repeat(3, max-content);
    justify-content: end;
    column-gap: var(--space-14);
  }

  .footer-logo-link {
    position: absolute;
    top: 0;
    right: 0;
  }
}

@media screen and (min-width: 1024px) {
  .footer-container {
    width: calc(100% - var(--sizes-container-padding-lg));
  }
}
