.list-unstyled {
  list-style-type: none;
}

.list-position-inside {
  list-style-position: inside;
}

.list-striped li {
  padding: var(--space-6) var(--space-5);
  list-style-type: none;
  list-style-position: inside;
  border-radius: var(--radii-xs);
}

.list-striped-primary li:nth-child(odd) {
  background-color: var(--colors-pistachio);
}

.list-striped-secondary li:nth-child(odd) {
  background-color: var(--colors-lemon);
}

.list-review {
  padding-top: var(--space-5);
  border-top: 1px solid var(--colors-orangi-secondary);
}
.list-review-no-decoration {
  border-top: 0;
  padding-top: 0;
}
.list-review > .form-group + .form-group {
  margin-top: var(--space-12);
}

.list-review dt {
  margin-bottom: var(--space-2);
  font-weight: var(--fontWeights-bold);
}
.list-review dd {
  margin-bottom: var(--space-3);
}

.list-review > * .content-card {
  box-shadow: none;
  padding: 0;
}

.list-review > * .content-card.contact-card:after {
  top: var(--space-1);
}

.numbered-list {
  list-style-type: none;
}
.numbered-list li {
  counter-increment: step-counter;
  margin-bottom: var(--space-6);
}
.numbered-list li:before {
  content: counter(step-counter);
  margin-right: var(--space-4);
  background-color: var(--colors-pukepoto);
  color: var(--colors-ma);
  font-weight: var(--fontWeights-bold);
  padding: var(--space-1) var(--space-3);
  width: 30px;
  height: 30px;
  border-radius: 100%;
}
