h1,
.size-h1,
.font-size-h1 {
  font-size: var(--fontSizes-h1);
}
h1,
.size-h1 {
  font-weight: var(--fontWeights-black);
  line-height: var(--lineHeights-short);
  margin-bottom: var(--space-4);
}

h2,
.size-h2,
.font-size-h2 {
  font-size: var(--fontSizes-h2);
}
h2,
.size-h2 {
  line-height: var(--lineHeights-short);
  font-weight: var(--fontWeights-bold);
  margin-bottom: var(--space-4);
}

h3,
.size-h3,
.font-size-h3 {
  font-size: var(--fontSizes-h3);
}
h3,
.size-h3 {
  line-height: var(--lineHeights-base);
  font-weight: var(--fontWeights-bold);
  margin-bottom: var(--space-4);
}

h4,
.size-h4,
.font-size-h4 {
  font-size: var(--fontSizes-h4);
}
h4,
.size-h4 {
  line-height: var(--lineHeights-base);
  font-weight: var(--fontWeights-bold);
  margin-bottom: var(--space-1);
}

p {
  font-family: var(--fonts-body);
  font-size: var(--fontSizes-p);
  line-height: var(--lineHeights-base);
}

.font-size-p {
  font-size: var(--fontSizes-p);
}

.font-size-p2 {
  font-size: var(--fontSizes-p2);
}

p:not(:last-child) {
  margin-bottom: var(--space-2);
}

.watermark {
  font-size: 72px;
  left: 50%;
  opacity: 0.1;
  position: absolute;
  z-index: -1;
}

@media screen and (min-width: 768px) {
  p:not(:last-child) {
    margin-bottom: var(--space-4);
  }

  .watermark {
    font-size: 128px;
  }
}
