.layout {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  height: 100%;
  min-height: 100vh;
  background: var(--colors-background) no-repeat center center fixed;
  box-sizing: border-box;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  margin-bottom: var(--space-8);
  z-index: 2;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  box-sizing: border-box;
}

.app-logo {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  font-family: var(--fonts-body);
  font-size: clamp(1.6rem, 1.44rem + 0.5vw, 2.4rem);
  font-weight: var(--fontWeights-bold);
  line-height: var(--lineHeights-logo);
  color: var(--colors-pukepoto);
  white-space: normal;
  outline: currentcolor none medium;
  padding-top: 0.8rem;
  padding-left: 2.4rem;
  padding-right: 4rem;
  margin-right: 0.4rem;
  width: clamp(20.8rem, 20.8rem + 2.5vw, 40rem);
  height: 6.4rem;
}

.app-logo:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: calc(100% + 3.2rem);
  height: 100%;
  border-top: 6.4rem solid var(--colors-kowhai);
  border-right: 4.5rem solid transparent;
  z-index: -1;
}

.app-logo:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: calc(100% + 1.6rem);
  height: 100%;
  border-top: 6.4rem solid var(--colors-ma);
  border-right: 4.5rem solid transparent;
  z-index: -1;
}

.grecaptcha-badge {
  z-index: 999;
}

.moh-logo {
  display: flex;
  padding-right: var(--space-4);
}

.moh-logo > a {
  width: 20rem;
  outline: currentcolor none medium;
  transition: var(--transitions-button);
}

.moh-logo > a > img {
  width: 100%;
  transition: inherit;
}

.moh-logo > img:hover {
  opacity: 0.6;
}

.main {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
}

.main-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  opacity: 1;
  position: relative;
  transform: none;
  overflow: hidden;
}

.main-content {
  font-size: var(--fontSizes-p);
  margin-left: auto;
  margin-right: auto;
  max-width: 1680px;
  width: calc(100% - var(--sizes-container-padding-sm));
  min-height: 100%;
  z-index: 1;
}

.page-content {
  display: flex;
  flex-direction: column;
  padding-bottom: var(--space-20);
  ---stackSpacing: var(--space-4);
}

.page-content .section + .section {
  padding: var(---stackSpacing) 0 0;
}

nav[role='navigation'] {
  margin: 2.4rem auto;
  padding: 0 2em;
  max-width: 600px;
}

nav ul {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  padding-left: 0;
}

nav ul li {
  margin-right: 2.4rem;
}

ol {
  list-style-position: inside;
}

hr {
  width: 100%;
  margin: var(--spacing) 0;
}

.content-card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: var(--space-4);
  background-color: var(--colors-ma);
  border-radius: var(--radii-default);
  box-shadow: var(--shadows-card);
}

.content-card-padding-m {
  padding: var(--space-6);
}

.content-card-padding-l {
  padding: var(--space-10);
}

.content-card > *:not(:last-child) {
  margin-bottom: var(--space-3);
}

.content-card.contact-card:after {
  content: '';
  position: absolute;
  top: var(--space-4);
  right: var(--space-4);
  width: 24px;
  height: 24px;
  background: url('../images/icons/person.svg') no-repeat;
  background-size: 24px 24px;
}

.content-card.diary-icons-card {
  margin-top: var(--spacing-xxxl);
  padding-top: var(--spacing-xxxl);
}

.content-card.diary-icons-card .nz-covid-tracer-icon {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-50%);
}

.content-card.diary-icons-card .rippl-icon {
  position: absolute;
  top: 0;
  left: calc(87px + var(--space-4));
  transform: translateY(-50%);
}

.content-card.timeline-card .size-h3 {
  margin-bottom: var(--space-2);
}
.content-card.timeline-card h4 {
  font-weight: var(--fontWeights-medium);
  font-size: 1.6rem;
  line-height: var(--lineHeights-base);
}

.content-card.timeline-card:after {
  content: '';
  position: absolute;
  top: var(--space-4);
  right: var(--space-4);
  width: 35px;
  height: 35px;
  background: url('../images/icons/location.svg') no-repeat;
  background-size: 35px 35px;
}
.content-card.timeline-card .contact-list-item:before {
  display: block;
  content: '';
  width: 18px;
  height: 18px;
  background: url('../images/icons/person.svg') no-repeat;
  background-size: 18px 18px;
}

.content-card > * .content-card-tags {
  display: flex;
  flex-direction: column;
  width: fit-content;
}

.content-card-tags > .tag + .tag {
  margin-top: var(--space-2);
  width: fit-content;
}

.four-oh-four.img {
  padding-top: var(--space-20);
}

@media screen and (min-width: 768px) {
  .header {
    padding-right: var(--space-8);
  }

  .moh-logo {
    padding-right: 0;
  }

  .moh-logo > a {
    width: 25rem;
  }

  .app-logo {
    padding-left: 4.8rem;
    padding-right: 8rem;
    margin-right: 4.8rem;
    padding-top: 2rem;
    width: clamp(23.2rem, 23.2rem + 5vw, 40rem);
    height: 9.6rem;
  }

  .app-logo:before {
    border-top: 9.6rem solid var(--colors-kowhai);
    border-right: 8rem solid transparent;
  }

  .app-logo:after {
    border-top: 9.6rem solid var(--colors-ma);
    border-right: 8rem solid transparent;
  }

  .content-card > *:not(:last-child) {
    margin-bottom: var(--space-4);
  }

  .main-content {
    width: calc(100% - var(--sizes-container-padding-md));
  }

  .page-content {
    ---stackSpacing: var(--space-6);
  }
}

@media screen and (min-width: 1024px) {
  .header {
    padding-right: var(--space-10);
    margin-bottom: var(--space-20);
    flex-direction: row;
  }

  .main-content {
    width: calc(100% - var(--sizes-container-padding-lg));
  }

  .content-card > *:not(:last-child) {
    margin-bottom: var(--space-6);
  }

  .page-content {
    ---stackSpacing: var(--space-8);
  }
}
