.link {
  font-weight: var(--fontWeights-bold);
  transition: var(--transitions-button);
}

.link-underline {
  text-decoration: underline;
}

.link-external-cms,
.link-external {
  position: relative;
  margin-right: 20px;
}
.link-external {
  white-space: nowrap;
}
.link-external-cms {
  white-space: nowrap;
}

.link-external-cms:after,
.link-external:after {
  content: '';
  position: absolute;
  width: 14px;
  height: 14px;
  background: url('../images/icons/external.svg') no-repeat;
  background-size: 14px 14px;
  right: -18px;
  bottom: 2px;
}

.link-external-cms:after {
  background: url('../images/icons/external-cms.svg') no-repeat;
  background-size: 14px 14px;
}

.form-back-link {
  line-height: var(--sizes-12);
  font-weight: var(--fontWeights-bold);
  color: var(--colors-pukepoto);
  text-align: center;
  vertical-align: middle;
}

.form-back-link:hover {
  text-decoration: underline;
}

.back-arrow-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: var(--space-4);
  font-size: var(--fontSizes-p);
}

.back-arrow-link:hover {
  text-decoration: underline;
}

.back-arrow-link:before {
  display: block;
  content: '';
  margin-right: var(--space-2);
  width: 24px;
  height: 24px;
  background: url('../images/icons/arrow-left.svg') no-repeat;
  background-size: 24px 24px;
}

.privacy-link a {
  color: var(--colors-kahurangi);
  font-weight: var(--fontWeights-semibold);
  text-decoration: underline;
  transition: var(--transitions-button);
  outline: currentcolor none medium;
}

.privacy-link p {
  margin-bottom: 0;
}

.cms-link a {
  color: var(--colors-kahurangi);
  font-weight: var(--fontWeights-semibold);
  text-decoration: underline;
  transition: var(--transitions-button);
  outline: currentcolor none medium;
}

@media screen and (min-width: 768px) {
  .link-external-cms,
  .link-external {
    margin-right: 24px;
  }

  .link-external-cms:after,
  .link-external:after {
    width: 16px;
    height: 16px;
    background-size: 16px 16px;
    right: -20px;
    bottom: 4px;
  }
}
