.accordion {
  background-color: var(--colors-ma);
  border-radius: var(--radii-default);
}

.accordion.accordion-timeline-review {
  border-radius: 0;
  border-top: 1px solid var(--colors-ma);
}

.accordion-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: auto;
  font-size: var(--fontSizes-h3);
  padding: var(--space-6) var(--space-4);
  background-color: var(--colors-ma);
  color: var(--font-color);
  outline: currentcolor none medium;
  border-radius: var(--radii-default);
}

.accordion-button-font-size-small {
  font-size: var(--fontSizes-p);
}

.accordion-button:focus {
  box-shadow: none;
}

.accordion-button:after {
  display: block;
  content: '';
  margin-right: var(--space-2);
  width: 16px;
  height: 16px;
  background: url('../images/icons/caret.svg') no-repeat;
  background-size: 16px 16px;
}

.accordion.active.accordion-timeline-review {
  border-top: 1px solid var(--colors-orangi-secondary);
}

.accordion.active .accordion-button {
  border-radius: var(--radii-default) var(--radii-default) 0 0;
}

.accordion.active .accordion-button.accordion-button-blue {
  background-color: #bbdaf7;
  border-radius: var(--radii-default) var(--radii-default) 0 0;
}

.accordion.active > .accordion-button:after {
  transform: rotate(180deg);
}

.accordion-panel {
  padding: 0 var(--space-4) var(--space-5);
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  background-color: var(--colors-ma);
  border-radius: 0 0 var(--radii-default) var(--radii-default);
}

.accordion-panel-blue {
  background-color: #bbdaf7;
}

/* For nested accordions (: */
.accordion-panel .accordion-button,
.accordion-panel .accordion-panel {
  padding-left: var(--space-1);
  padding-right: var(--space-1);
}

.accordion-panel .accordion-button {
  font-size: var(--space-5);
}
