.popup {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: var(--colors-modal-overlay-background);
  z-index: 999;
}

.popup-content {
  position: fixed;
  top: 40%;
  left: 50%;
  z-index: 1000;
  transform: translate(-50%, -50%);
  max-width: 400px;
  width: calc(100% - var(--sizes-container-padding-sm));
  font-weight: var(--fontWeights-semibold);
  align-items: center;
  justify-content: center;
  border-radius: var(--radii-default);
  background-color: var(--colors-ma);
  text-align: center;
}

.close-button-icon {
  position: absolute;
  top: var(--space-4);
  right: var(--space-4);
  cursor: pointer;
}

.emergency-popup-content {
  overflow: auto;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1000;
  transform: translate(-50%, -50%);
  max-width: 400px;
  width: calc(100% - var(--sizes-container-padding-sm));
  align-items: center;
  justify-content: center;
  border-radius: var(--radii-default);
  background-color: var(--colors-ma);
  text-align: center;
}

.emergency-popup-content .image-container {
  display: flex;
  justify-content: center;
}

.emergency-popup-content img {
  width: var(--space-8);
  height: var(--space-8);
}

.error-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .emergency-popup-content {
    max-width: 420px;
  }
}

.emergency-cms {
  width: 100%;
  text-align: left;
}
.emergency-cms ul {
  list-style-position: outside;
}

.emergency-popup-content button {
  margin-top: var(--space-4);
  display: flex;
  white-space: normal;
  padding: 1.4rem var(--space-5);
}

@media only screen and (max-height: 800px) {
  .emergency-popup-content {
    max-height: calc(100% - var(--sizes-container-padding-sm));
    justify-content: start;
  }
  .emergency-popup-content button {
    padding: var(--space-5);
  }
}
