.page-title {
  white-space: break-spaces;
}

.title {
  display: block;
  position: relative;
  width: 100%;
  margin-bottom: 0;
}

.title-icon-location:after,
.title-icon-person:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  width: 24px;
  height: 28px;
  transform: translateY(-50%);
  background-size: 24px 28px;
}
.title-icon-location:after {
  background: url('../images/icons/location.svg') no-repeat;
}
.title-icon-person:after {
  background: url('../images/icons/person.svg') no-repeat;
}
