.select-input,
.time-picker-input-ie,
input[type='number'],
input[type='time'],
input[type='text'],
input[type='tel'] {
  width: 100%;
  padding: var(--space-3) var(--space-4);
  margin: var(--space-2) 0;
  display: inline-block;
  background-color: var(--colors-ma);
  border: var(--input-border);
  border-radius: var(--radii-sm);
  box-sizing: border-box;
}

input[type='radio'] {
  margin-right: var(--space-2);
}

input:focus {
  box-shadow: var(--shadows-outline);
  outline: none;
}

.time-input {
  font-family: var(--fonts-body) !important;
}

.date-of-birth-inputs {
  display: flex;
  flex-direction: row;
}

.date-of-birth-inputs > .date-of-birth-input {
  margin-right: 1.6rem;
  width: 8rem;
}

label {
  display: inline-block;
  margin: var(--space-1) 0;
}

label.is-invalid {
  color: var(--input-border-color-invalid);
}

.form-group {
  display: flex;
  flex-direction: column;
  white-space: pre-line;
}

.form-group + .form-section,
.form-group + .form-group {
  margin-top: 3.2rem;
}

.form-section {
  background-color: var(--colors-ma);
  border-radius: var(--radii-default) var(--radii-default) 0 0;
  display: flex;
  flex-direction: column;
  padding: var(--space-4) var(--space-4) 0 var(--space-4);
  width: 100%;
}

.form-section + .form-section {
  border-radius: 0;
  padding: var(--space-4) var(--space-4) 0 var(--space-4);
}

.form-section > .address-label {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.last-form-section + .form-section {
  border-top-left-radius: var(--radii-default) var(--radii-default);
  border-top-right-radius: var(--radii-default) var(--radii-default);
}

.form-section.last-form-section {
  border-bottom-left-radius: var(--radii-default) var(--radii-default);
  border-bottom-right-radius: var(--radii-default) var(--radii-default);
  margin-bottom: var(--space-6);
  padding-bottom: var(--space-4);
}

.form-section legend,
.form-section > label,
.form-group > label,
.form-group legend {
  font-size: var(--fontSizes-p);
  font-weight: var(--fontWeights-bold);
  margin-bottom: 0;
}

.form-section legend:not(:last-child),
.form-section > label:not(:last-child),
.form-group > label:not(:last-child),
.form-group legend:not(:last-child) {
  margin-bottom: 0.8rem;
}

.form-section .input-label,
.form-group .input-label {
  font-weight: var(--fontWeights-bold);
  font-size: var(--fontSizes-h4);
}

.date-of-birth-input > label {
  font-size: var(--fontSizes-p2);
  font-weight: bold;
  margin-bottom: 0;
}

.help-text {
  margin: var(--space-1) 0 var(--space-2);
  font-size: var(--fontSizes-p2);
}

.form-list {
  list-style-position: inside;
}

.select-input,
.time-picker-input-ie.is-invalid,
input[type='time'].is-invalid,
input[type='number'].is-invalid,
input[type='text'].is-invalid,
input[type='tel'].is-invalid {
  border-color: var(--input-border-color-invalid);
}

.invalid-feedback {
  width: 100%;
  color: var(--feedback-color-invalid);
}

.button-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 3.2rem;
}

.divider {
  width: calc(100%);
  border: 1px solid;
}
.divider-white {
  border-color: var(--colors-ma);
}
.divider-blue {
  border-color: var(--colors-orangi-dark);
}

.label,
.input-label {
  white-space: pre-wrap;
}

.input-label.error {
  color: var(--colors-whero);
}

.optional-required {
  margin-bottom: var(--space-2);
}

.optional-required span {
  color: var(--colors-primary-75);
  font-size: var(--fontSizes-p2);
  line-height: var(--lineHeights-base);
}

@media screen and (min-width: 768px) {
  .form {
    align-self: center;
    width: 620px;
  }
}
