.landing-title {
  font-family: var(--fonts-body);
  font-size: var(--fontSizes-display);
  font-weight: var(--fontWeights-black);
  line-height: var(--lineHeights-shorter);
  margin-bottom: var(--space-4);
}

.landing-button {
  margin: var(--spacing-xs) 0;
}

.landing-text {
  margin: var(--spacing-xs) 0;
}

.landing-image {
  margin-bottom: -80px;
  width: var(--sizes-200);
}

.landing-image-background {
  z-index: -2;
  position: absolute;
  bottom: 10%;
  max-height: 100%;
  max-width: none;
  height: 40%;
  left: 0;
}

@media screen and (min-width: 768px) {
  .landing-image {
    padding-top: var(--space-10);
  }
}

@media screen and (min-width: 1024px) {
  .landing-image-background {
    left: 0;
    top: 50%;
    height: 100%;
    transform: translateY(-50%);
  }
}

@media screen and (min-height: 1300px) {
  .landing-image {
    padding-top: var(--space-50);
  }
}

@media screen and (min-width: 2560px) {
  .landing-title {
    line-height: var(--spacing-xxxxl);
    margin-bottom: var(--space-6);
  }
  .landing-image {
    padding-top: var(--space-20);
    width: var(--sizes-300);
  }
}
