.tag {
  padding: var(--space-1) var(--space-2);
  background-color: var(--colors-lemon);
  border-radius: var(--radii-default);
  width: fit-content;
}

.tag:not(:last-child) {
  margin-right: var(--space-3);
}

.tag-variant-primary {
  background-color: var(--colors-lemon);
}

.tag-variant-secondary {
  background-color: var(--colors-pink);
}

.tag-code {
  letter-spacing: 0.1em;
}

.date-tag {
  align-items: center;
  background-color: white;
  border: 5px solid;
  border-radius: var(--radii-default);
  display: flex;
  flex-shrink: 0;
  font-size: var(--fontSizes-p);
  font-weight: var(--fontWeights-bold);
  height: var(--space-12);
  justify-content: center;
  line-height: 100%;
  margin-right: var(--space-8);
  padding: var(--space-4);
  position: relative;
  text-align: center;
  width: var(--space-12);
}

.date-tag.whero {
  border-color: var(--colors-whero);
}

.date-tag.kowhai {
  border-color: var(--colors-kowhai);
}

.date-tag.kakariki {
  border-color: var(--colors-kakariki);
}

.date-tag-divider {
  height: 100%;
  position: absolute;
  left: calc((var(--space-12) - 3px) / 2);
  border-right: 3px dashed #7e94b5;
}
