.invalid-input-container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: var(--space-1);
  color: var(--feedback-color-invalid);
}

.invalid-input-container img {
  height: var(--space-4);
  margin-right: var(--space-2);
  margin-top: var(--space-1);
}

.error-summary {
  background-color: var(--colors-ma);
  border-radius: var(--radii-default);
  border-left: var(--space-4) var(--colors-whero) solid;
  display: flex;
  flex-direction: column;
  padding: var(--space-4);
  margin-bottom: var(--space-6);
  white-space: pre-line;
  width: 100%;
}

.error-summary-title {
  align-items: center;
  color: var(--colors-whero);
  display: flex;
  font-size: var(--space-4);
  font-weight: var(--fontWeights-bold);
  line-height: var(--lineHeights-base);
  margin-bottom: var(--space-4);
}

.error-summary-title img {
  height: var(--space-6);
  margin-right: var(--space-1);
}

.error-summary-link {
  align-items: start;
  border-radius: 2px;
  color: var(--colors-whero);
  cursor: pointer;
  font-size: var(--fontSizes-p);
  font-weight: var(--fontWeights-normal);
  height: auto;
  justify-content: start;
  line-height: var(--lineHeights-base);
  padding: 0;
  text-align: start;
  text-decoration-line: underline;
  white-space: break-spaces;
}

.error-summary-link + .error-summary-link {
  margin-top: var(--space-3);
}
