.four-oh-four-image {
  padding-top: var(--space-30);
  margin-bottom: -80px;
  width: var(--sizes-200);
}

@media screen and (min-width: 768px) {
  .four-oh-four-image {
    padding-top: var(--space-10);
  }
}

@media screen and (min-height: 1300px) {
  .four-oh-four-image {
    padding-top: var(--space-50);
  }
}

@media screen and (min-width: 2560px) {
  .four-oh-four-image {
    padding-top: var(--space-20);
    width: var(--sizes-300);
  }
}
