.address-autocomplete__input[type='text'] {
  margin-bottom: 0;
}

.address-autocomplete__suggestions {
  margin: 0;
  background: white;
  list-style: none;
  border: 1px solid var(--address-autocomplete-border-color);
  border-top: 0px;
  border-radius: var(--address-autocomplete-border-radius);
}

.address-autocomplete__suggestions__item {
  border-bottom: 1px solid var(--address-autocomplete-border-color);
  padding: var(--address-autocomplete-suggestion-padding);
  cursor: pointer;
}

.address-autocomplete__suggestions__item:first-child {
  border-radius: var(--address-autocomplete-border-radius)
    var(--address-autocomplete-border-radius) 0 0;
}

.address-autocomplete__suggestions__item--active {
  background: #eef0f2;
  font-weight: bold;
}

.af_footer {
  padding: var(--spacing-s) var(--address-autocomplete-suggestion-padding);
  white-space: normal;
}

.af_empty {
  white-space: normal;
  padding: var(--spacing-s) var(--address-autocomplete-suggestion-padding);
}
