.CalendarDay {
  font-size: var(--fontSizes-p);
}

.CalendarDay__default {
  position: relative;
  color: var(--colors-pukepoto);
  background: var(--colors-ma);
}
.CalendarDay__default:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  margin-left: auto;
  margin-right: auto;
  width: var(--sizes-8);
  height: var(--sizes-8);
  border-radius: 100%;
  border-style: none;
  padding-top: var(--space-2);
  text-align: center;
  line-height: var(--lineHeights-base);
  z-index: -1;

  background-color: var(--colors-orangi-dark);
}
.CalendarDay__today:before {
  background-color: var(--colors-kowhai);
}
.CalendarDay__default:hover::before {
  background-color: var(--colors-kikorangi);
}

.CalendarDay__selected,
.CalendarDay__selected:active,
.CalendarDay__selected:hover {
  background: var(--colors-pukepoto);
  color: var(--colors-ma);
  font-weight: var(--fontWeights-bold);
}
.CalendarDay__selected:before {
  background-color: var(--colors-pukepoto);
}
.CalendarDay__selected:hover::before {
  background-color: var(--colors-kahurangi);
}

.CalendarDay__blocked_out_of_range {
  opacity: 0.25;
}

.CalendarDay__blocked_out_of_range,
.CalendarDay__blocked_out_of_range:active,
.CalendarDay__blocked_out_of_range:hover {
  color: var(--colors-pukepoto);
}
.CalendarDay__blocked_out_of_range:before {
  background-color: transparent;
}
.CalendarDay__blocked_out_of_range:hover::before {
  background-color: transparent;
}

.CalendarMonth_caption {
  color: var(--font-color);
  text-align: left;
  font-size: var(--fontSizes-p);
  line-height: var(--lineHeights-base);
}

.DayPicker_transitionContainer {
  min-height: 340px;
  background-color: var(--colors-ma);
  border-radius: var(--radii-default);
  box-shadow: var(--shadows-card);
}

.calendar__prev,
.calendar__next {
  position: absolute;
}

.calendar__prev,
.calendar__next {
  top: var(--space-6);
}

.calendar__prev {
  right: var(--space-12);
  transform: rotate(90deg);
}

.calendar__next {
  right: var(--space-3);
  transform: rotate(-90deg);
}

.CalendarDay:focus {
  outline: 2px dotted var(--colors-pukepoto);
  outline-offset: 2px;
}

.DayPicker_weekHeader_li small {
  font-size: var(--fontSizes-p2);
  line-height: var(--lineHeights-medium);
  --tw-text-opacity: 1;
  color: rgba(57, 57, 57, var(--tw-text-opacity));
}

.DayPicker,
.CalendarMonthGrid,
.CalendarMonth {
  background-color: transparent !important;
}

.CalendarDay__default {
  border-style: none;
  background-color: transparent;
}
.CalendarDay__default:hover {
  border-style: none;
  background-color: transparent;
}
