button,
.button {
  display: inline-flex;
  appearance: none;
  align-items: center;
  justify-content: center;
  transition: var(--transitions-button);
  user-select: none;
  position: relative;
  white-space: nowrap;
  vertical-align: middle;
  height: var(--sizes-12);
  border-radius: var(--button-border-radius);
  color: var(--colors-ma);
  font-family: var(--fonts-body);
  font-size: var(--fontSizes-p);
  font-weight: var(--fontWeights-bold);
  line-height: var(--lineHeights-base);
  padding: 0 var(--space-7);
}
button:focus,
.button:focus {
  box-shadow: var(--shadows-outline);
  outline: none;
}

.button-variant-primary {
  background-color: var(--colors-pukepoto);
}
.button-variant-primary:hover {
  background-color: var(--colors-korora);
}

.button-variant-secondary {
  background-color: var(--colors-orangi-dark);
  color: var(--colors-pukepoto);
}
.button-variant-secondary:hover {
  background-color: var(--colors-kahurangi);
  color: var(--colors-ma);
}

.button-variant-tertiary {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 5px;
  width: 100%;
}
.button-variant-tertiary:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.button-variant-card {
  justify-content: flex-start;
  padding: var(--space-6) var(--space-4);
  height: auto;
  background-color: var(--colors-ma);
  color: var(--colors-pukepoto);
  border-radius: var(--radii-default);
  box-shadow: var(--shadows-card);
}
.button-variant-card:hover {
  background-color: var(--colors-orangi);
}

.button-variant-add {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: var(--space-6) var(--space-4);
  height: auto;
  background-color: var(--colors-ma);
  color: var(--colors-pukepoto);
  border-radius: var(--radii-default);
  box-shadow: var(--shadows-card);
}
.button-variant-add:hover {
  background-color: var(--colors-orangi);
}
.button-variant-add:before {
  content: '';
  margin-right: var(--space-2);
  width: 24px;
  height: 24px;
  background: url('../images/icons/plus.svg') no-repeat;
  background-size: 24px 24px;
  flex-shrink: 0;
}

.button-variant-tag {
  justify-content: flex-start;
  padding: var(--space-2) var(--space-4);
  height: auto;
  font-weight: var(--fontWeights-regular);
  background-color: var(--colors-pistachio);
  color: var(--colors-pukepoto);
  border-radius: var(--radii-sm);
}
.button-variant-tag:hover {
  background-color: var(--colors-orangi-dark);
}

.button-variant-delete {
  justify-content: flex-start;
  padding: var(--space-2) var(--space-4);
  height: auto;
  font-weight: var(--fontWeights-regular);
  background-color: var(--colors-pistachio);
  color: var(--colors-pukepoto);
  border-radius: var(--radii-sm);
}
.button-variant-delete:hover {
  background-color: var(--colors-orangi-dark);
}
.button-variant-delete:before {
  display: block;
  content: '';
  margin-right: var(--space-2);
  width: 16px;
  height: 16px;
  background: url('../images/icons/cross.svg') no-repeat;
  background-size: 16px 16px;
}

.button-variant-edit {
  justify-content: flex-start;
  padding: var(--space-2) var(--space-4);
  height: auto;
  font-weight: var(--fontWeights-regular);
  background-color: var(--colors-pistachio);
  color: var(--colors-pukepoto);
  border-radius: var(--radii-sm);
}
.button-variant-edit:hover {
  background-color: var(--colors-orangi-dark);
}
.button-variant-edit:before {
  display: block;
  content: '';
  margin-right: var(--space-2);
  width: 18px;
  height: 18px;
  background: url('../images/icons/edit.svg') no-repeat;
  background-color: var(--colors-pukepoto);
  background-size: 10px 10px;
  background-position: center;
  border-radius: 100%;
}

.button-variant-link {
  padding: 0;
  height: auto;
  font-weight: var(--fontWeights-medium);
  line-height: var(--lineHeights-base);
  color: var(--colors-kahurangi);
  border-radius: var(--radii-sm);
}
.button-variant-link:hover {
  text-decoration: underline;
}
.button-variant-link:focus {
  box-shadow: var(--shadows-outline-sm);
  outline: none;
}

.form-group > .button-variant-primary {
  align-self: center;
}

.button-text-wrap {
  text-align: left;
}

@media screen and (min-width: 640px) {
  .button-variant-tertiary {
    width: auto;
  }
}
