:root {
  /* Colors */
  --colors-pukepoto: #002e6e;
  --colors-pukepoto-decimal-rgb: 0, 46, 110;
  --colors-korora: #1c5da3;
  --colors-kahurangi: #0071bc;
  --colors-kikorangi: #55a3eb;
  --colors-orangi: #f1f8fd;
  --colors-orangi-secondary: #f18700;
  --colors-orangi-medium: #e7f1f9;
  --colors-orangi-dark: #ddedfb;
  --colors-ma: #ffffff;
  --colors-pango: #000000;
  --colors-kowhai: #fcba29;
  --colors-karaka: #fa922b;
  --colors-kakariki: #1f816c;
  --colors-whero: #dd1b00;
  --colors-waiporoporo: #5c1893;
  --colors-lemon: #ffe582;
  --colors-pistachio: #cee7ec;
  --colors-pink: #ffe2da;
  --colors-gradient-1: #bbdaf5;
  --colors-gradient-2: #ddecf8;
  --colors-background: var(--colors-orangi-medium);
  --colors-primary-75: #3f6192;
  --colors-primary-50: #7e94b5;
  --colors-tertiary-110: #f0a500;
  --colors-tertiary-100: #fcba29;
  --colors-tertiary-25: #fbebc8;
  --colors-outline: #7eb6dc;
  /* --background-gradient: linear-gradient(
    180deg,
    var(--colors-orangi-dark) 0%,
    var(--colors-orangi) 15rem,
    var(--colors-orangi) 100%
  ); */

  /* General */
  --font-color: var(--colors-pukepoto);

  /* Address autocomplete */
  --address-autocomplete-border-color: var(--input-border-color);

  /* Form misc. */
  --feedback-color-invalid: var(--colors-whero);

  /* Inputs */
  --input-border-color: rgba(var(--colors-pukepoto-decimal-rgb), 0.7);
  --input-border-color-invalid: var(--colors-whero);

  /* Settings */

  /* Default spacing */
  --spacing: 2.4rem; /* 24px  (--space-6)*/

  --spacing-xxxs: calc(var(--spacing) / 6); /* 4px (--space-1) */
  --spacing-xxs: calc(var(--spacing) - 1.6rem); /* 8px (--space-2) */
  --spacing-xs: calc(var(--spacing) * 0.5); /* 12px (--space-3) */
  --spacing-s: calc(var(--spacing) - 0.8rem); /* 16px (--space-4) */
  --spacing-l: calc(var(--spacing) + 0.8rem); /* 32px */
  --spacing-xl: calc(var(--spacing) + 1.6rem); /* 40px (--space-10) */
  --spacing-xxl: calc(var(--spacing) + 2.4rem); /* 48px */
  --spacing-xxxl: calc(var(--spacing) + 4rem); /* 64px */
  --spacing-xxxxl: calc(var(--spacing) + 5.6rem); /* 80px */

  /* Border radius */
  --radii-default: 0.8rem;
  --radii-sm: 0.4rem;
  --radii-xs: 0.2rem;

  /* Fonts */
  --fonts-body: Fira Sans, system-ui, sans-serif;

  /* Font weights */
  --fontWeights-black: 900;
  --fontWeights-bold: 700;
  --fontWeights-extrabold: 800;
  --fontWeights-hairline: 100;
  --fontWeights-light: 300;
  --fontWeights-medium: 500;
  --fontWeights-normal: 400;
  --fontWeights-regular: 400;
  --fontWeights-semibold: 600;
  --fontWeights-thin: 200;

  /* Line heights */
  --lineHeights-normal: normal;
  --lineHeights-shorter: 1.15;
  --lineHeights-short: 1.2;
  --lineHeights-logo: 1.25;
  --lineHeights-medium: 1.3;
  --lineHeights-base: 1.6;

  /* Accordion */
  --accordion-button-padding: 18px;

  /* Address autocomplete */
  --address-autocomplete-suggestion-padding: var(--spacing-xxs);

  /* Button */
  --transitions-button: all 200ms ease-in-out;
  --button-border-radius: 30px;

  /* Inputs */
  --input-border: 1px solid var(--input-border-color);

  --colors-modal-overlay-background: rgba(0, 46, 110, 0.75);

  /* Spaces */
  /* Anatomic is setting html font size to be 62.5% (10px). * 1.6 sets the value back to be relative to 16px */
  --space-0: 0;
  --space-1: calc(0.25rem * 1.6);
  --space-2: calc(0.5rem * 1.6);
  --space-3: calc(0.75rem * 1.6);
  --space-4: calc(1rem * 1.6);
  --space-5: calc(1.25rem * 1.6);
  --space-6: calc(1.5rem * 1.6);
  --space-7: calc(1.75rem * 1.6);
  --space-8: calc(2rem * 1.6);
  --space-9: calc(2.25rem * 1.6);
  --space-10: calc(2.5rem * 1.6);
  --space-11: calc(2.75rem * 1.6);
  --space-12: calc(3rem * 1.6);
  --space-14: calc(3.5rem * 1.6);
  --space-15: calc(3.75rem * 1.6);
  --space-16: calc(4rem * 1.6);
  --space-18: calc(4.5rem * 1.6);
  --space-20: calc(5rem * 1.6);
  --space-30: calc(7.5rem * 1.6);
  --space-38: calc(9.5rem * 1.6);
  --space-40: calc(10rem * 1.6);
  --space-50: calc(12.5rem * 1.6);
  --space-container-padding-sm: var(--space-8);
  --space-container-padding-md: var(--space-16);
  --space-container-padding-lg: var(--space-30);

  /* Sizes */
  /* Anatomic is setting html font size to be 62.5% (10px). * 1.6 sets the value back to be relative to 16px */
  --sizes-0: 0;
  --sizes-1: calc(0.25rem * 1.6);
  --sizes-2: calc(0.5rem * 1.6);
  --sizes-3: calc(0.75rem * 1.6);
  --sizes-4: calc(1rem * 1.6);
  --sizes-5: calc(1.25rem * 1.6);
  --sizes-6: calc(1.5rem * 1.6);
  --sizes-7: calc(1.75rem * 1.6);
  --sizes-8: calc(2rem * 1.6);
  --sizes-9: calc(2.25rem * 1.6);
  --sizes-10: calc(2.5rem * 1.6);
  --sizes-11: calc(2.75rem * 1.6);
  --sizes-12: calc(3rem * 1.6);
  --sizes-14: calc(3.5rem * 1.6);
  --sizes-15: calc(3.75rem * 1.6);
  --sizes-16: calc(4rem * 1.6);
  --sizes-18: calc(4.5rem * 1.6);
  --sizes-20: calc(5rem * 1.6);
  --sizes-30: calc(7.5rem * 1.6);
  --sizes-50: calc(12.5rem * 1.6);
  --sizes-200: calc(50rem * 1.6);
  --sizes-300: calc(75rem * 1.6);
  --sizes-container-padding-sm: var(--sizes-8);
  --sizes-container-padding-md: var(--sizes-16);
  --sizes-container-padding-lg: var(--sizes-30);

  /* Font Sizes */
  --fontSizes-display: clamp(4rem, calc(4rem + ((1vw - 0.64rem) * 5)), 10rem);
  --fontSizes-h1: clamp(3.2rem, calc(3.2rem + ((1vw - 0.32rem) * 0.5)), 4rem);
  --fontSizes-h2: clamp(2.4rem, calc(2.4rem + ((1vw - 0.32rem) * 0.5)), 3.2rem);
  --fontSizes-h3: clamp(2rem, calc(2rem + ((1vw - 0.32rem) * 0.25)), 2.4rem);
  --fontSizes-h4: clamp(
    1.8rem,
    calc(1.8rem + ((1vw - 0.32rem) * 0.25)),
    2.4rem
  );
  --fontSizes-p: clamp(1.6rem, calc(1.6rem + ((1vw - 0.32rem) * 0.25)), 2rem);
  --fontSizes-p2: clamp(
    1.4rem,
    calc(1.4rem + ((1vw - 0.32rem) * 0.25)),
    1.8rem
  );

  /* Radii */
  --radii-icon: 26.25%;
  --radii-round: 50%;

  /* Shadows */
  --shadows-outline: 0 0 0 4px rgba(66, 153, 225, 0.6);
  --shadows-card: 0px 4px 50px 0px #0071bc1a;

  /* Z Index */
  --zIndices-pageLoader: 8000;
  --zIndices-dialog: 7999;
  --zIndices-maintenanceBanner: 7998;
}
