.content-cms.break-spaces {
  white-space: break-spaces;
}

.content-card.content-cms > p:not(:last-child) {
  margin-bottom: 0;
}

.content-card.content-cms > p,
.content-card.content-cms > h1,
.content-card.content-cms > h2,
.content-card.content-cms > h3 {
  margin-bottom: 0;
}

.content-cms > h1:not(:last-child),
.content-cms > h2:not(:last-child),
.content-cms > h3:not(:last-child) {
  margin-top: var(--space-3);
}

/* Required so that empty paragraphs result in a line break showing */
.content-cms p:after {
  content: '';
  display: inline-block;
}

.content-cms > table {
  margin-top: var(--space-8);
}

.content-cms > table:not(:last-child) {
  margin-bottom: var(--space-8);
}

.content-cms table,
.content-cms td {
  border-color: var(--colors-orangi-medium);
  border-style: solid;
}

.content-cms table tbody tr:first-child {
  font-weight: var(--fontWeights-bold);
}

.content-cms table tbody tr:not(:first-child) td {
  padding-top: var(--space-3);
}

.content-cms table tbody tr:not(:last-child) td {
  padding-bottom: var(--space-3);
  border-bottom-width: 1px;
}

.content-cms table tbody tr td:not(:first-child) {
  padding-left: var(--space-3);
}

.content-cms table tbody tr td:not(:last-child) {
  padding-right: var(--space-3);
  border-right-width: 1px;
}

.list-string-items,
.content-cms ul {
  font-size: var(--fontSizes-p);
  list-style-position: inside;
  margin-bottom: var(--space-3);
  padding-left: 1.6rem;
}

.content-cms ol {
  font-size: var(--fontSizes-p);
  list-style-position: initial;
  margin-bottom: var(--space-3);
  padding-left: 3.2rem;
}

.content-cms img:not(:last-child) {
  margin-bottom: var(--space-4);
}

.msd-details {
  background-color: var(--colors-pistachio);
  padding: var(--space-4);
  margin: var(--space-3) 0;
}
